import React from "react";
import PropTypes from "prop-types";
import { withStyles } from "@material-ui/core/styles";
import { bindActionCreators } from "redux";
import { connect } from "react-redux";
import {actionCreators} from "../../store/LandingPage";

import Button from "@material-ui/core/Button";
import Grid from "@material-ui/core/Grid";


const styles = theme => ({ 
    container: {
        padding: "15px 0px",
        backgroundColor: "#ffffff",
    },   
    paper: {           
        fontSize: "18px",
    },
    noneDecor: {
        textDecoration: "none",
    },
});

class LandingBlockButton extends React.Component {

    render() {
        const {classes} = this.props;  
        return (
         
            <Grid className = {classes.container}
                style={this.props.landingPage.selectLanding.blocks[this.props.itemNum].params.mainText.background} item xs={12} container justify="center">
                <Grid item xs={6} container justify="center" >
                <a className = {classes.noneDecor} target="_blank" rel="noopener noreferrer"
                href={this.props.landingPage.selectLanding.blocks[this.props.itemNum].params.url.link}>
                    <Button variant="contained" style={this.props.landingPage.selectLanding.blocks[this.props.itemNum].params.mainText.style}>
                        {this.props.landingPage.selectLanding.blocks[this.props.itemNum].params.mainText.text}
                    </Button>
                </a>
                </Grid>
            </Grid>           
         
            
        )
    }
}

LandingBlockButton.propTypes = {
    classes: PropTypes.object.isRequired,
};

function mapStateToProps(state) {
    return {
        landingPage: state.landingPage, 
    }    
}

function matchDispatchToProps(dispatch){
    return bindActionCreators(actionCreators,dispatch)    
}


export default withStyles(styles)(connect(mapStateToProps, matchDispatchToProps)(LandingBlockButton));