import React from "react";
import { bindActionCreators } from "redux";
import { connect } from "react-redux";
import { actionCreators } from "../../store/LandingPage";
import { withStyles } from "@material-ui/core/styles";
import { ChromePicker } from 'react-color';

import Slider from '@material-ui/core/Slider';
import ExpansionPanel from '@material-ui/core/ExpansionPanel';
import ExpansionPanelSummary from '@material-ui/core/ExpansionPanelSummary';
import ExpansionPanelDetails from '@material-ui/core/ExpansionPanelDetails';
import Typography from '@material-ui/core/Typography';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';

import TextField from "@material-ui/core/TextField";
import Grid from "@material-ui/core/Grid";
import Button from "@material-ui/core/Button";
import FormControlLabel from "@material-ui/core/FormControlLabel";
import FormControl from "@material-ui/core/FormControl";
import FormLabel from "@material-ui/core/FormLabel";
import Radio from "@material-ui/core/Radio";
import RadioGroup from "@material-ui/core/RadioGroup";

const styles = theme => ({
  p10: {
    padding: '10px',
  },
});

class BlockHeaderProp extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      urlImg: {},
    };
  }

  handleEditBlockProp = (event, value, nameBlockProp, styleName) => {
    this.props.changeBlockValueF(nameBlockProp, 'style', value, styleName)
  }

  handleChangeColor = (color) => {
    this.props.changeBlockValueF('header', 'style', color.hex, 'color')
  };

  handleChange = (url) => {
    this.setState({ urlImg: url });
    console.log(this.state.urlImg);
  }
  render() {
    return (
      <Grid container>
        <Grid item xs={12}>
          <ExpansionPanel>
            <ExpansionPanelSummary
              expandIcon={<ExpandMoreIcon />}
              aria-controls="panel1a-content"
              id="panel1a-header"
            >
              <Typography>Надзаголовок</Typography>
            </ExpansionPanelSummary>
            <ExpansionPanelDetails>
              <Grid container>
                <Grid item xs={12}>
                  <TextField
                    label="текст надзаголовка"
                    name="header"
                    value={this.props.landingPage.selectBlock.params.header.text}
                    variant="outlined"
                    onChange={e => this.props.changeBlockValueF(e.target.name, "text", e.target.value)}
                    fullWidth={true}
                    margin="normal"
                  />
                </Grid>
                <Grid item xs={12}>
                  <FormControl >
                    <FormLabel component="legend">Шрифт</FormLabel>
                    <RadioGroup
                      aria-label="gender"
                      name="fontFamily"
                      value={this.props.landingPage.selectBlock.params.header.style.fontFamily}
                      onChange={(nullE, value) => this.handleEditBlockProp(null, value, "header", "fontFamily")}
                    >
                      <FormControlLabel value="serif" control={<Radio />} label="serif" />
                      <FormControlLabel value="sans-serif" control={<Radio />} label="sans-serif" />
                      <FormControlLabel value="monospace" control={<Radio />} label="monospace" />
                      <FormControlLabel value="cursive" control={<Radio />} label="cursive" />
                      <FormControlLabel value="fantasy" control={<Radio />} label="fantasy" />
                      <FormControlLabel value="system-ui" control={<Radio />} label="system-ui" />                      
                      <FormControlLabel value="inherit" control={<Radio />} label="inherit" />
                    </RadioGroup>
                  </FormControl>
                </Grid>
                <Grid item xs={12}>
                <Typography id="discrete-slider" gutterBottom>
                  Цвет текста
                        <ChromePicker color={this.props.landingPage.selectBlock.params.header.style.color}
                    onChangeComplete={(color) => this.handleEditBlockProp('null', color.hex, "header", "color")} />
                </Typography>
                </Grid>
                <Grid item xs={12}>
                <Typography id="discrete-slider" gutterBottom>
                  Размер текста: {this.props.landingPage.selectBlock.params.header.style.fontSize}

                  <Slider
                    defaultValue={this.props.landingPage.selectBlock.params.header.style.fontSize}
                    aria-labelledby="discrete-slider"
                    valueLabelDisplay="auto"
                    onChange={(event, fontSize) => this.handleEditBlockProp(event, fontSize, "header", "fontSize")}
                    step={2}
                    marks
                    min={12}
                    max={64}
                  />
                </Typography>
                </Grid>
              </Grid>
            </ExpansionPanelDetails>
          </ExpansionPanel>
        </Grid>

        <Grid item xs={12}>
          <ExpansionPanel>
            <ExpansionPanelSummary
              expandIcon={<ExpandMoreIcon />}
              aria-controls="panel1a-content"
              id="panel1a-header"
            >
              <Typography>Заголовок</Typography>
            </ExpansionPanelSummary>
            <ExpansionPanelDetails>
              <Grid container>
                <Grid item xs={12}>
                  <TextField
                    label="текст заголовка"
                    name="mainText"
                    value={this.props.landingPage.selectBlock.params.mainText.text}
                    variant="outlined"
                    onChange={e => this.props.changeBlockValueF(e.target.name, "text", e.target.value)}
                    fullWidth={true}
                    margin="normal"
                    multiline
                    rows="3"
                  />
                </Grid>
                <Grid item xs={12}>
                  <FormControl >
                    <FormLabel component="legend">Шрифт</FormLabel>
                    <RadioGroup
                      aria-label="gender"
                      name="fontFamily"
                      value={this.props.landingPage.selectBlock.params.mainText.style.fontFamily}
                      onChange={(nullE, value) => this.handleEditBlockProp(null, value, "mainText", "fontFamily")}
                    >
                      <FormControlLabel value="serif" control={<Radio />} label="serif" />
                      <FormControlLabel value="sans-serif" control={<Radio />} label="sans-serif" />
                      <FormControlLabel value="monospace" control={<Radio />} label="monospace" />
                      <FormControlLabel value="cursive" control={<Radio />} label="cursive" />
                      <FormControlLabel value="fantasy" control={<Radio />} label="fantasy" />
                      <FormControlLabel value="system-ui" control={<Radio />} label="system-ui" />                      
                      <FormControlLabel value="inherit" control={<Radio />} label="inherit" />
                    </RadioGroup>
                  </FormControl>
                </Grid>
                <Grid item xs={12}>
                <Typography id="discrete-slider" gutterBottom>
                  Цвет текста
                        <ChromePicker color={this.props.landingPage.selectBlock.params.mainText.style.color}
                    onChangeComplete={(color) => this.handleEditBlockProp('null', color.hex, "mainText", "color")} />
                </Typography>
                </Grid>
                <Grid item xs={12}>
                <Typography id="discrete-slider" gutterBottom>
                  Размер текста: {this.props.landingPage.selectBlock.params.mainText.style.fontSize}

                  <Slider
                    defaultValue={this.props.landingPage.selectBlock.params.mainText.style.fontSize}
                    aria-labelledby="discrete-slider"
                    valueLabelDisplay="auto"
                    onChange={(event, fontSize) => this.handleEditBlockProp(event, fontSize, "mainText", "fontSize")}
                    step={2}
                    marks
                    min={12}
                    max={64}
                  />
                </Typography>
                </Grid>
              </Grid>
            </ExpansionPanelDetails>
          </ExpansionPanel>
        </Grid>

        <Grid item xs={12}>
          <ExpansionPanel>
            <ExpansionPanelSummary
              expandIcon={<ExpandMoreIcon />}
              aria-controls="panel1a-content"
              id="panel1a-header"
            >
              <Typography>Описание</Typography>
            </ExpansionPanelSummary>
            <ExpansionPanelDetails>
              <Grid container>
                <Grid item xs={12}>
                  <TextField
                    label="текст описания"
                    name="underText"
                    value={this.props.landingPage.selectBlock.params.underText.text}
                    variant="outlined"
                    onChange={e => this.props.changeBlockValueF(e.target.name, "text", e.target.value)}
                    fullWidth={true}
                    margin="normal"
                    multiline
                    rows="3"
                  />
                </Grid>
                <Grid item xs={12}>
                  <FormControl >
                    <FormLabel component="legend">Шрифт</FormLabel>
                    <RadioGroup
                      aria-label="gender"
                      name="fontFamily"
                      value={this.props.landingPage.selectBlock.params.underText.style.fontFamily}
                      onChange={(nullE, value) => this.handleEditBlockProp(null, value, "underText", "fontFamily")}
                    >
                      <FormControlLabel value="serif" control={<Radio />} label="serif" />
                      <FormControlLabel value="sans-serif" control={<Radio />} label="sans-serif" />
                      <FormControlLabel value="monospace" control={<Radio />} label="monospace" />
                      <FormControlLabel value="cursive" control={<Radio />} label="cursive" />
                      <FormControlLabel value="fantasy" control={<Radio />} label="fantasy" />
                      <FormControlLabel value="system-ui" control={<Radio />} label="system-ui" />                      
                      <FormControlLabel value="inherit" control={<Radio />} label="inherit" />
                    </RadioGroup>
                  </FormControl>
                </Grid>
                <Grid item xs={12}>
                <Typography id="discrete-slider" gutterBottom>
                  Цвет текста
                        <ChromePicker color={this.props.landingPage.selectBlock.params.underText.style.color}
                    onChangeComplete={(color) => this.handleEditBlockProp('null', color.hex, "underText", "color")} />
                </Typography>
                </Grid>
                <Grid item xs={12}>
                <Typography id="discrete-slider" gutterBottom>
                  Размер текста: {this.props.landingPage.selectBlock.params.underText.style.fontSize}

                  <Slider
                    defaultValue={this.props.landingPage.selectBlock.params.underText.style.fontSize}
                    aria-labelledby="discrete-slider"
                    valueLabelDisplay="auto"
                    onChange={(event, fontSize) => this.handleEditBlockProp(event, fontSize, "underText", "fontSize")}
                    step={2}
                    marks
                    min={12}
                    max={64}
                  />
                </Typography>
                </Grid>
              </Grid>
            </ExpansionPanelDetails>
          </ExpansionPanel>
        </Grid>

        <Grid item xs={12}>
          <ExpansionPanel>
            <ExpansionPanelSummary
              expandIcon={<ExpandMoreIcon />}
              aria-controls="panel1a-content"
              id="panel1a-header"
            >
              <Typography>Изображение</Typography>
            </ExpansionPanelSummary>
            <ExpansionPanelDetails>
              <Grid container>
                <Grid item xs={12}>
                  <TextField
                    label="ссылка на изображение"
                    name="underText"
                    variant="outlined"
                    onChange={e => this.handleChange(e.target.value)}
                    fullWidth={true}
                    margin="normal"
                    multiline
                    rows="2"
                  />
                </Grid>
                <Button title="Загрузить" onClick={() => this.props.changeIMG(this.state.urlImg)}
                  variant="outlined" color="primary">Загрузить изображение
                  </Button>
              </Grid>
            </ExpansionPanelDetails>
          </ExpansionPanel>
        </Grid>

      </Grid>
    );
  }
}

function mapStateToProps(state) {
  return {
    landingPage: state.landingPage,
  }
}

function matchDispatchToProps(dispatch) {
  return bindActionCreators(actionCreators, dispatch)
}



export default withStyles(styles)(connect(mapStateToProps, matchDispatchToProps)(BlockHeaderProp));