import React from "react";
import { withStyles } from "@material-ui/core/styles";
import { bindActionCreators } from "redux";
import { connect } from "react-redux";

import {actionCreators} from "../../store/LandingPage";

import Grid from "@material-ui/core/Grid";

const styles = theme => ({  
    textCenter: {
        textAlign: "center",
        fontSize: "24px"
    },
    videoConteiner: {
        position:'relative',
        paddingBottom:'56.25%',
        paddingTop:'30px',
        height:'0',
        overflow:'hidden',
    },
    iframeYoutube: {
        position:'absolute',
        top:'0',
        left:'0',
        width:'100%',
        height:'100%',
    }
});
class LandingBlockVideo extends React.Component {

    render() {
        const {classes} = this.props; 
        return ( 
       
            <Grid container style={this.props.landingPage.selectLanding.blocks[this.props.itemNum].params.video.background} justify={this.props.landingPage.selectLanding.blocks[this.props.itemNum].params.video.background.justifyContent}>            
                <Grid item xs={12} sm={this.props.landingPage.selectLanding.blocks[this.props.itemNum].params.video.width.number}>    
                    <div style={this.props.landingPage.selectLanding.blocks[this.props.itemNum].params.video.style}>{this.props.landingPage.selectLanding.blocks[this.props.itemNum].params.video.text}</div>                
                    <div className={classes.videoConteiner}>
                        <iframe className={classes.iframeYoutube} width="560" height="315" src={this.props.landingPage.selectLanding.blocks[this.props.itemNum].params.video.url} frameborder="0" allowfullscreen title={this.props.landingPage.selectLanding.blocks[this.props.itemNum].params.video.text}></iframe>
                    </div>
                </Grid>               
            </Grid>
        
            
        )
    }
}


function mapStateToProps(state) {
    return {
        landingPage: state.landingPage,
    }    
}

function matchDispatchToProps(dispatch){
    return bindActionCreators(actionCreators, dispatch)    
}


export default withStyles(styles)(connect(mapStateToProps, matchDispatchToProps)(LandingBlockVideo));