import React from "react";
import { bindActionCreators } from "redux";
import { connect } from "react-redux";
import { actionCreators } from "../../store/LandingPage";
import { withStyles } from "@material-ui/core/styles";

import Grid from "@material-ui/core/Grid";

const styles = theme => ({
    container: {
        padding: '15px 0px',
        backgroundColor: '#ffffff',
    },
    img: {
        width: '40%',
        marginLeft: 'auto',
        marginRight: 'auto',
        display: 'block',
    },

});


class LandingBlockTextImg extends React.Component {

    render() {
        return (
            <Grid item xs={12} sm={12} container>
                <Grid style={this.props.landingPage.selectLanding.blocks[this.props.itemNum].params.leftCol.background} item xs={this.props.landingPage.selectLanding.blocks[this.props.itemNum].params.leftCol.textWidht.number}>
                    <div item style={this.props.landingPage.selectLanding.blocks[this.props.itemNum].params.leftColHeader.style} container >
                        {this.props.landingPage.selectLanding.blocks[this.props.itemNum].params.leftColHeader.text}
                    </div>
                    <div item style={this.props.landingPage.selectLanding.blocks[this.props.itemNum].params.leftColUnderText.style} container >
                        {this.props.landingPage.selectLanding.blocks[this.props.itemNum].params.leftColUnderText.text}
                    </div>
                    <div item style={this.props.landingPage.selectLanding.blocks[this.props.itemNum].params.leftColMainText.style} container >
                        {this.props.landingPage.selectLanding.blocks[this.props.itemNum].params.leftColMainText.text}
                    </div>
                </Grid>
                <Grid style={this.props.landingPage.selectLanding.blocks[this.props.itemNum].params.rightCol.background} item xs={12} sm={this.props.landingPage.selectLanding.blocks[this.props.itemNum].params.rightCol.textWidht.number} container justify="center" >
                    <Grid item style={this.props.landingPage.selectLanding.blocks[this.props.itemNum].params.rightCol.style} container >
                        <img alt="img" style={this.props.landingPage.selectLanding.blocks[this.props.itemNum].params.rightCol.img} src={this.props.landingPage.selectLanding.blocks[this.props.itemNum].params.rightCol.imgUrl} />
                    </Grid>
                </Grid>
            </Grid>
        )
    }
}

function mapStateToProps(state) {
    return {
        landingPage: state.landingPage,
    }
}

function matchDispatchToProps(dispatch) {
    return bindActionCreators(actionCreators, dispatch)
}


export default withStyles(styles)(connect(mapStateToProps, matchDispatchToProps)(LandingBlockTextImg));