import React from "react";
import { bindActionCreators } from "redux";
import { connect } from "react-redux";
import {actionCreators} from "../../store/LandingPage";


import { HuePicker} from 'react-color';
import Grid from "@material-ui/core/Grid";
import TextField from "@material-ui/core/TextField";
import Slider from '@material-ui/core/Slider';
import ExpansionPanel from '@material-ui/core/ExpansionPanel';
import ExpansionPanelSummary from '@material-ui/core/ExpansionPanelSummary';
import ExpansionPanelDetails from '@material-ui/core/ExpansionPanelDetails';
import Typography from '@material-ui/core/Typography';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';


class BlockButtonProp extends React.Component {

  
  handleEditBlockProp = ( event, value, nameBlockProp, style ,styleName) => { 
    this.props.changeBlockValueF(nameBlockProp, style, value, styleName) 
  }

    render(){ 
        return(   
          <Grid container>

            <Grid item xs={12}>
              <ExpansionPanel>
                <ExpansionPanelSummary
                  expandIcon={<ExpandMoreIcon />}
                  aria-controls="panel1a-content"
                  id="panel1a-header"
                >
                <Typography>Праметры для кнопки</Typography>
              </ExpansionPanelSummary>
                <ExpansionPanelDetails>
                  <Grid container>  
                    <Grid item xs={12}>
                        <TextField
                          label="Текст"
                          name="mainText"
                          value={this.props.landingPage.selectBlock.params.mainText.text}
                          variant="outlined"                              
                          onChange={e => this.props.changeBlockValueF(e.target.name, "text", e.target.value)}
                          fullWidth={true}
                          margin="normal"
                        />
                    </Grid> 
                   
                    <Typography id="discrete-slider" gutterBottom>
                        Цвет текста
                        <HuePicker  color={ this.props.landingPage.selectBlock.params.mainText.style.color }
                        onChangeComplete={(color) => this.handleEditBlockProp('null', color.hex, "mainText", "style", "color")} />
                    </Typography>
                  
                      <Typography id="discrete-slider" gutterBottom>
                        Размер текста: {this.props.landingPage.selectBlock.params.mainText.style.fontSize}
                        
                      <Slider
                        defaultValue={this.props.landingPage.selectBlock.params.mainText.style.fontSize}
                        aria-labelledby="discrete-slider"
                        valueLabelDisplay="auto"
                        onChange={(event, fontSize) => this.handleEditBlockProp(event, fontSize, "mainText", "style", "fontSize")}
                        step={2}
                        marks
                        min={8}
                        max={48}
                      />
                      </Typography>

                      <Grid item xs={12}>
                        <TextField
                          label="Ссылка"
                          name="url"
                          value={this.props.landingPage.selectBlock.params.url.link}
                          variant="outlined"                              
                          onChange={e => this.props.changeBlockValueF(e.target.name, "link", e.target.value)}
                          fullWidth={true}
                          margin="normal"
                        />
                      </Grid>

                      <Typography id="discrete-slider" gutterBottom>
                        Цвет кнопки
                        <HuePicker  color={ this.props.landingPage.selectBlock.params.mainText.style.backgroundColor }
                        onChangeComplete={(color) => this.handleEditBlockProp('null', color.hex, "mainText", "style", "backgroundColor")} />
                    </Typography>

                  </Grid>
                </ExpansionPanelDetails>
              </ExpansionPanel>  
            </Grid> 

            <Grid item xs={12}>
              <ExpansionPanel>
                <ExpansionPanelSummary
                  expandIcon={<ExpandMoreIcon />}
                  aria-controls="panel1a-content"
                  id="panel1a-header"
                >
                <Typography>Цвет фона</Typography>
              </ExpansionPanelSummary>
              <ExpansionPanelDetails>
                <Grid container>              
                    <Typography id="discrete-slider" gutterBottom>
                        Выберите цвет фона для блока
                        <HuePicker  color={ this.props.landingPage.selectBlock.params.mainText.background.backgroundColor }
                        onChangeComplete={(color) => this.handleEditBlockProp('null', color.hex, "mainText", "background", "backgroundColor")} />
                    </Typography>                   
                  </Grid>
                </ExpansionPanelDetails>
              </ExpansionPanel>  
            </Grid> 



          </Grid> 
        );
    }
}

function mapStateToProps(state) {
    return {
      landingPage: state.landingPage,  
    }    
}

function matchDispatchToProps(dispatch){
    return bindActionCreators(actionCreators,dispatch)    
}



export default connect(mapStateToProps, matchDispatchToProps)(BlockButtonProp);