import React from "react";
import { bindActionCreators } from "redux";
import { connect } from "react-redux";
import {actionCreators} from "../../store/LandingPage";
import { withStyles } from "@material-ui/core/styles";

import Grid from "@material-ui/core/Grid";

const styles = theme => ({  
    container: {
        padding: '15px 0px',
        backgroundColor: '#ffffff',
    },  
    img: {  
        width:'40%',
        marginLeft: 'auto',
        marginRight: 'auto',
        display: 'block',
    },
    
});


class LandingBlockImgText extends React.Component {

    render() { 
        return (
            <Grid item xs={12} sm={12} container>
                 <Grid style={this.props.landingPage.selectLanding.blocks[this.props.itemNum].params.leftCol.background} item xs={this.props.landingPage.selectLanding.blocks[this.props.itemNum].params.leftCol.textWidht.number} container justify="center" >
                    <Grid item style={this.props.landingPage.selectLanding.blocks[this.props.itemNum].params.leftCol.style} container >
                        <img alt="img" style={this.props.landingPage.selectLanding.blocks[this.props.itemNum].params.leftCol.img} src={this.props.landingPage.selectLanding.blocks[this.props.itemNum].params.leftCol.imgUrl} />
                    </Grid>
                </Grid>
                <Grid style={this.props.landingPage.selectLanding.blocks[this.props.itemNum].params.rightCol.background} item xs={12} sm={this.props.landingPage.selectLanding.blocks[this.props.itemNum].params.rightCol.textWidht.number}>
                    <div item style={this.props.landingPage.selectLanding.blocks[this.props.itemNum].params.rightColHeader.style} container >
                        {this.props.landingPage.selectLanding.blocks[this.props.itemNum].params.rightColHeader.text}
                    </div>
                    <div item style={this.props.landingPage.selectLanding.blocks[this.props.itemNum].params.rightColUnderText.style} container >
                        {this.props.landingPage.selectLanding.blocks[this.props.itemNum].params.rightColUnderText.text}
                    </div>
                    <div item style={this.props.landingPage.selectLanding.blocks[this.props.itemNum].params.rightColMainText.style} container >
                        {this.props.landingPage.selectLanding.blocks[this.props.itemNum].params.rightColMainText.text}
                    </div>
                </Grid>               
            </Grid>
            
        )
    }
}

function mapStateToProps(state) {
    return {
      landingPage: state.landingPage,  
    }    
}

function matchDispatchToProps(dispatch){
    return bindActionCreators(actionCreators,dispatch)    
}


export default withStyles(styles)(connect(mapStateToProps, matchDispatchToProps)(LandingBlockImgText));