import React from "react";
import PropTypes from "prop-types";
import { bindActionCreators } from "redux";
import { connect } from "react-redux";
import {actionCreators} from "../../store/LandingPage";

import Grid from "@material-ui/core/Grid";

class LandingBlockTextBlock extends React.Component {

    render() { 
        return (
         
            <Grid style={this.props.landingPage.selectLanding.blocks[this.props.itemNum].params.mainText.background} item xs={12} container justify="center" >
                <Grid item xs={this.props.landingPage.selectLanding.blocks[this.props.itemNum].params.mainText.textWidht.number} container >
                    <div style={this.props.landingPage.selectLanding.blocks[this.props.itemNum].params.mainText.style}>
                        {this.props.landingPage.selectLanding.blocks[this.props.itemNum].params.mainText.text}
                    </div>
                </Grid>
            </Grid>     
         )
    }
}

LandingBlockTextBlock.propTypes = {
    classes: PropTypes.object.isRequired,
    
};

function mapStateToProps(state) {
    return { 
        landingPage: state.landingPage,
    }
}

function matchDispatchToProps(dispatch){
    return bindActionCreators(actionCreators,dispatch)    
}


export default connect(mapStateToProps, matchDispatchToProps)(LandingBlockTextBlock);