import React from "react";
import { bindActionCreators } from "redux";
import { connect } from "react-redux";
import {actionCreators} from "../../store/LandingPage";
import { withStyles } from "@material-ui/core/styles";

import Grid from "@material-ui/core/Grid";

const styles = theme => ({  
    container: {
        padding: '15px 0px',
        backgroundColor: '#ffffff',
    },  
    img: {  
        width:'100%',
        marginLeft: 'auto',
        marginRight: 'auto',
        display: 'block',
        padding: '20px',
    },
    
});


class LandingBlockThreeImg extends React.Component {

    render() { 
        const {classes} = this.props;
        return (
            <Grid item xs={12} container justify="center" >                 
                <Grid container justify="center" item xs={12} sm={4}>
                    <img alt="img" className={classes.img} src={this.props.landingPage.selectLanding.blocks[this.props.itemNum].params.Img.imgUrl} />
                </Grid> 
                <Grid container justify="center" item xs={12} sm={4}>
                    <img alt="img" className={classes.img} src={this.props.landingPage.selectLanding.blocks[this.props.itemNum].params.Img2.imgUrl} />
                </Grid>   
                <Grid container justify="center" item xs={12} sm={4}>
                    <img alt="img" className={classes.img} src={this.props.landingPage.selectLanding.blocks[this.props.itemNum].params.Img3.imgUrl} />
                </Grid>    
            </Grid> 
            
        )
    }
}

function mapStateToProps(state) {
    return {
      landingPage: state.landingPage,  
    }    
}

function matchDispatchToProps(dispatch){
    return bindActionCreators(actionCreators,dispatch)    
}


export default withStyles(styles)(connect(mapStateToProps, matchDispatchToProps)(LandingBlockThreeImg));