import React from "react";
import { bindActionCreators } from "redux";
import { connect } from "react-redux";
import { actionCreators } from "../../store/LandingPage";
import { withStyles } from "@material-ui/core/styles";

import TextField from "@material-ui/core/TextField";
import Grid from "@material-ui/core/Grid";
import CancelIcon from '@material-ui/icons/Cancel';
import Toolbar from "@material-ui/core/Toolbar";
import IconButton from "@material-ui/core/IconButton";

const styles = theme => ({
  p10: {
    padding: '10px',
  },
});

class BlockSocietyProp extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      urlImg: {},
    };
  }

  handleChange = (url) => {
    this.setState({ urlImg: url });
    console.log(this.state.urlImg);
  }
  render() {
    return (
      <Grid container>
        <Grid item xs={12}>
          <Toolbar variant="dense">
            <TextField
              label="Instagram PAGE LINK"
              name="Instagram"
              value={this.props.landingPage.selectBlock.params.Instagram.url}
              variant="outlined"
              onChange={e => this.props.changeBlockValueF(e.target.name, "url", e.target.value)}
              fullWidth={true}
              margin="normal"
            />
            <IconButton onClick={e => this.props.changeBlockValueF("Instagram", "url", "")}>
              <CancelIcon />
            </IconButton>
          </Toolbar>
        </Grid>
        <Grid item xs={12}>

          <Toolbar variant="dense">
            <TextField
              label="Facebook PAGE LINK"
              name="Facebook"
              value={this.props.landingPage.selectBlock.params.Facebook.url}
              variant="outlined"
              onChange={e => this.props.changeBlockValueF(e.target.name, "url", e.target.value)}
              fullWidth={true}
              margin="normal"
            />
            <IconButton onClick={e => this.props.changeBlockValueF("Facebook", "url", "")}>
              <CancelIcon />
            </IconButton>
          </Toolbar>
        </Grid>
        <Grid item xs={12}>
          <Toolbar variant="dense">
            <TextField
              label="Twitter PAGE LINK"
              name="Twitter"
              value={this.props.landingPage.selectBlock.params.Twitter.url}
              variant="outlined"
              onChange={e => this.props.changeBlockValueF(e.target.name, "url", e.target.value)}
              fullWidth={true}
              margin="normal"
            />
            <IconButton onClick={e => this.props.changeBlockValueF("Twitter", "url", "")}>
              <CancelIcon />
            </IconButton>
          </Toolbar>
        </Grid>
        <Grid item xs={12}>
          <Toolbar variant="dense">
            <TextField
              label="YouTube PAGE LINK"
              name="YouTube"
              value={this.props.landingPage.selectBlock.params.YouTube.url}
              variant="outlined"
              onChange={e => this.props.changeBlockValueF(e.target.name, "url", e.target.value)}
              fullWidth={true}
              margin="normal"
            />
            <IconButton onClick={e => this.props.changeBlockValueF("YouTube", "url", "")}>
              <CancelIcon />
            </IconButton>
          </Toolbar>
        </Grid>
        <Grid item xs={12}>
          <Toolbar variant="dense">
            <TextField
              label="LinkedIn PAGE LINK"
              name="LinkedIn"
              value={this.props.landingPage.selectBlock.params.LinkedIn.url}
              variant="outlined"
              onChange={e => this.props.changeBlockValueF(e.target.name, "url", e.target.value)}
              fullWidth={true}
              margin="normal"
            />
            <IconButton onClick={e => this.props.changeBlockValueF("LinkedIn", "url", "")}>
              <CancelIcon />
            </IconButton>
          </Toolbar>
        </Grid>
        <Grid item xs={12}>
          <Toolbar variant="dense">
            <TextField
              label="Telegram PAGE LINK"
              name="Telegram"
              value={this.props.landingPage.selectBlock.params.Telegram.url}
              variant="outlined"
              onChange={e => this.props.changeBlockValueF(e.target.name, "url", e.target.value)}
              fullWidth={true}
              margin="normal"
            />
            <IconButton onClick={e => this.props.changeBlockValueF("Telegram", "url", "")}>
              <CancelIcon />
            </IconButton>
          </Toolbar>
        </Grid>
      </Grid>
    );
  }
}

function mapStateToProps(state) {
  return {
    landingPage: state.landingPage,
  }
}

function matchDispatchToProps(dispatch) {
  return bindActionCreators(actionCreators, dispatch)
}



export default withStyles(styles)(connect(mapStateToProps, matchDispatchToProps)(BlockSocietyProp));