import React from "react";
import { bindActionCreators } from "redux";
import { connect } from "react-redux";
import { actionCreators } from "../../store/LandingPage";
import { ChromePicker } from 'react-color';

import TextField from "@material-ui/core/TextField";
import Grid from "@material-ui/core/Grid";

import Slider from '@material-ui/core/Slider';
import ExpansionPanel from '@material-ui/core/ExpansionPanel';
import ExpansionPanelSummary from '@material-ui/core/ExpansionPanelSummary';
import ExpansionPanelDetails from '@material-ui/core/ExpansionPanelDetails';
import Typography from '@material-ui/core/Typography';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import Radio from "@material-ui/core/Radio";
import RadioGroup from "@material-ui/core/RadioGroup";
import FormControlLabel from "@material-ui/core/FormControlLabel";
import FormControl from "@material-ui/core/FormControl";
import FormLabel from "@material-ui/core/FormLabel";
class BlockColumProp extends React.Component {

  handleEditBlockProp = (event, value, nameBlockProp, style, styleName) => {
    this.props.changeBlockValueF(nameBlockProp, style, value, styleName)
  }

  render() {
    return (

      <Grid container>
        <Grid item xs={12}>
          <ExpansionPanel>
            <ExpansionPanelSummary
              expandIcon={<ExpandMoreIcon />}
              aria-controls="panel1a-content"
              id="panel1a-header"
            >
              <Typography component={'span'}>Левая колонка</Typography>
            </ExpansionPanelSummary>
            <ExpansionPanelDetails>
              <Grid container>
                <Grid item xs={12}>
                  <TextField
                    label="Левая колонка"
                    name="leftCol"
                    value={this.props.landingPage.selectBlock.params.leftCol.text}
                    variant="outlined"
                    onChange={e => this.props.changeBlockValueF(e.target.name, "text", e.target.value)}
                    fullWidth={true}
                    margin="normal"
                    multiline
                    rows="4"
                  />
                </Grid>
                <Grid item xs={12}>
                  <FormControl >
                    <FormLabel component="legend">Шрифт</FormLabel>
                    <RadioGroup
                      aria-label="gender"
                      name="fontFamily"
                      value={this.props.landingPage.selectBlock.params.leftCol.style.fontFamily}
                      onChange={(nullE, value) => this.handleEditBlockProp('null', value, "leftCol", "style", "fontFamily")}
                    >
                      <FormControlLabel value="serif" control={<Radio />} label="serif" />
                      <FormControlLabel value="sans-serif" control={<Radio />} label="sans-serif" />
                      <FormControlLabel value="monospace" control={<Radio />} label="monospace" />
                      <FormControlLabel value="cursive" control={<Radio />} label="cursive" />
                      <FormControlLabel value="fantasy" control={<Radio />} label="fantasy" />
                      <FormControlLabel value="system-ui" control={<Radio />} label="system-ui" />
                      <FormControlLabel value="inherit" control={<Radio />} label="inherit" />
                    </RadioGroup>
                  </FormControl>
                </Grid>
                <Grid item xs={12}>
                <Typography component={'span'} id="discrete-slider" gutterBottom>
                  Цвет текста
                        <ChromePicker color={this.props.landingPage.selectBlock.params.leftCol.style.color}
                    onChangeComplete={(color) => this.handleEditBlockProp('null', color.hex, "leftCol", "style", "color")} />
                </Typography>
                </Grid>
                <Grid item xs={12}>
                <Typography component={'span'} id="discrete-slider" gutterBottom>
                  Размер текста: {this.props.landingPage.selectBlock.params.leftCol.style.fontSize}

                  <Slider
                    defaultValue={this.props.landingPage.selectBlock.params.leftCol.style.fontSize}
                    aria-labelledby="discrete-slider"
                    valueLabelDisplay="auto"
                    onChange={(event, fontSize) => this.handleEditBlockProp(event, fontSize, "leftCol", "style", "fontSize")}
                    step={2}
                    marks
                    min={12}
                    max={64}
                  />
                </Typography>
                </Grid>
                <Grid item xs={12}>
                  <Typography component={'span'} id="discrete-slider" gutterBottom>
                    Ширина блока с текстом: {this.props.landingPage.selectBlock.params.leftCol.textWidht.number}

                    <Slider
                      defaultValue={this.props.landingPage.selectBlock.params.leftCol.textWidht.number}
                      aria-labelledby="discrete-slider"
                      valueLabelDisplay="auto"
                      onChange={(event, number) => this.handleEditBlockProp(event, number, "leftCol", "textWidht", "number")}
                      step={2}
                      marks
                      min={2}
                      max={12}
                    />
                  </Typography>
                </Grid>
                <Grid item xs={12}>
                  <FormControl >
                    <FormLabel component="legend">Выравнивание текста</FormLabel>
                    <RadioGroup
                      aria-label="gender"
                      name="textAlign"
                      value={this.props.landingPage.selectBlock.params.leftCol.style.textAlign}
                      onChange={(nullE, value) => this.handleEditBlockProp(nullE, value, "leftCol", "style", "textAlign")}
                    >
                      <FormControlLabel value="left" control={<Radio />} label="Слева" />
                      <FormControlLabel value="center" control={<Radio />} label="Центр" />
                      <FormControlLabel value="right" control={<Radio />} label="Справа" />
                      <FormControlLabel value="justify" control={<Radio />} label="Поширине" />
                    </RadioGroup>
                  </FormControl>
                </Grid>
                <Grid container>
                  <Typography component={'span'} id="discrete-slider" gutterBottom>
                    Выберите цвет фона для блока
                        <ChromePicker color={this.props.landingPage.selectBlock.params.leftCol.background.backgroundColor}
                      onChangeComplete={(color) => this.handleEditBlockProp('null', color.hex, "leftCol", "background", "backgroundColor")} />
                  </Typography>
                </Grid>
              </Grid>
            </ExpansionPanelDetails>
          </ExpansionPanel>
        </Grid>

        <Grid item xs={12}>
          <ExpansionPanel>
            <ExpansionPanelSummary
              expandIcon={<ExpandMoreIcon />}
              aria-controls="panel1a-content"
              id="panel1a-header"
            >
              <Typography component={'span'}>Правая колонка</Typography>
            </ExpansionPanelSummary>
            <ExpansionPanelDetails>
              <Grid container>
                <Grid item xs={12}>
                  <TextField
                    label="Правая колонка"
                    name="rightCol"
                    value={this.props.landingPage.selectBlock.params.rightCol.text}
                    variant="outlined"
                    onChange={e => this.props.changeBlockValueF(e.target.name, "text", e.target.value)}
                    fullWidth={true}
                    margin="normal"
                    multiline
                    rows="4"
                  />
                </Grid>
                <Grid item xs={12}>
                  <FormControl >
                    <FormLabel component="legend">Шрифт</FormLabel>
                    <RadioGroup
                      aria-label="gender"
                      name="fontFamily"
                      value={this.props.landingPage.selectBlock.params.rightCol.style.fontFamily}
                      onChange={(nullE, value) => this.handleEditBlockProp('null', value, "rightCol", "style", "fontFamily")}
                    >
                      <FormControlLabel value="serif" control={<Radio />} label="serif" />
                      <FormControlLabel value="sans-serif" control={<Radio />} label="sans-serif" />
                      <FormControlLabel value="monospace" control={<Radio />} label="monospace" />
                      <FormControlLabel value="cursive" control={<Radio />} label="cursive" />
                      <FormControlLabel value="fantasy" control={<Radio />} label="fantasy" />
                      <FormControlLabel value="system-ui" control={<Radio />} label="system-ui" />
                      <FormControlLabel value="inherit" control={<Radio />} label="inherit" />
                    </RadioGroup>
                  </FormControl>
                </Grid>
                <Grid item xs={12}>
                <Typography component={'span'} id="discrete-slider" gutterBottom>
                  Цвет текста
                      <ChromePicker color={this.props.landingPage.selectBlock.params.rightCol.style.color}
                    onChangeComplete={(color) => this.handleEditBlockProp('null', color.hex, "rightCol", "style", "color")} />
                </Typography>
                </Grid>
                <Grid item xs={12}>
                <Typography component={'span'} id="discrete-slider" gutterBottom>
                  Размер текста: {this.props.landingPage.selectBlock.params.rightCol.style.fontSize}

                  <Slider
                    defaultValue={this.props.landingPage.selectBlock.params.rightCol.style.fontSize}
                    aria-labelledby="discrete-slider"
                    valueLabelDisplay="auto"
                    onChange={(event, fontSize) => this.handleEditBlockProp(event, fontSize, "rightCol", "style", "fontSize")}
                    step={2}
                    marks
                    min={12}
                    max={64}
                  />
                </Typography>
                </Grid>
                <Grid item xs={12}>
                  <Typography component={'span'} id="discrete-slider" gutterBottom>
                    Ширина блока с текстом: {this.props.landingPage.selectBlock.params.rightCol.textWidht.number}

                    <Slider
                      defaultValue={this.props.landingPage.selectBlock.params.rightCol.textWidht.number}
                      aria-labelledby="discrete-slider"
                      valueLabelDisplay="auto"
                      onChange={(event, number) => this.handleEditBlockProp(event, number, "rightCol", "textWidht", "number")}
                      step={2}
                      marks
                      min={2}
                      max={12}
                    />
                  </Typography>
                </Grid>
                <Grid item xs={12}>
                  <FormControl >
                    <FormLabel component="legend">Выравнивание текста</FormLabel>
                    <RadioGroup
                      aria-label="gender"
                      name="textAlign"
                      value={this.props.landingPage.selectBlock.params.rightCol.style.textAlign}
                      onChange={(nullE, value) => this.handleEditBlockProp(nullE, value, "rightCol", "style", "textAlign")}
                    >
                      <FormControlLabel value="left" control={<Radio />} label="Слева" />
                      <FormControlLabel value="center" control={<Radio />} label="Центр" />
                      <FormControlLabel value="right" control={<Radio />} label="Справа" />
                      <FormControlLabel value="justify" control={<Radio />} label="Поширине" />
                    </RadioGroup>
                  </FormControl>
                </Grid>
                <Grid container>
                  <Typography component={'span'} id="discrete-slider" gutterBottom>
                    Выберите цвет фона для блока
                      <ChromePicker color={this.props.landingPage.selectBlock.params.rightCol.background.backgroundColor}
                      onChangeComplete={(color) => this.handleEditBlockProp('null', color.hex, "rightCol", "background", "backgroundColor")} />
                  </Typography>
                </Grid>
              </Grid>
            </ExpansionPanelDetails>
          </ExpansionPanel>
        </Grid>
      </Grid>
    );
  }
}

function mapStateToProps(state) {
  return {
    landingPage: state.landingPage,

  }
}

function matchDispatchToProps(dispatch) {
  return bindActionCreators(actionCreators, dispatch)
}



export default connect(mapStateToProps, matchDispatchToProps)(BlockColumProp);