import React from "react";
import { withStyles } from "@material-ui/core/styles";
import { bindActionCreators } from "redux";
import { connect } from "react-redux";

import {actionCreators} from "../../store/LandingPage";

import Grid from "@material-ui/core/Grid";

const styles = theme => ({  
    container: {
        padding: '15px 0px',
        backgroundColor: '#ffffff',
    },
    p50: {
        padding: '20px',
        textAlign: 'justify',

    }
});
class LandingBlockColum extends React.Component {

    render() {
        const {classes} = this.props; 
        return ( 
        <div className={classes.root}>
            <Grid container>
                <Grid style={this.props.landingPage.selectLanding.blocks[this.props.itemNum].params.leftCol.background} item xs={12} sm={this.props.landingPage.selectLanding.blocks[this.props.itemNum].params.leftCol.textWidht.number} container justify="center" >
                    <Grid item  container >
                        <div style={this.props.landingPage.selectLanding.blocks[this.props.itemNum].params.leftCol.style}>
                            {this.props.landingPage.selectLanding.blocks[this.props.itemNum].params.leftCol.text}
                        </div>
                    </Grid>
                </Grid> 
                <Grid style={this.props.landingPage.selectLanding.blocks[this.props.itemNum].params.rightCol.background} item xs={12} sm={this.props.landingPage.selectLanding.blocks[this.props.itemNum].params.rightCol.textWidht.number} container justify="center" >
                    <Grid item  container >
                        <div style={this.props.landingPage.selectLanding.blocks[this.props.itemNum].params.rightCol.style}>
                            {this.props.landingPage.selectLanding.blocks[this.props.itemNum].params.rightCol.text}
                        </div>
                    </Grid>
                </Grid>
            </Grid>
        </div>
            
        )
    }
}


function mapStateToProps(state) {
    return {
        landingPage: state.landingPage,
    }    
}

function matchDispatchToProps(dispatch){
    return bindActionCreators(actionCreators, dispatch)    
}


export default withStyles(styles)(connect(mapStateToProps, matchDispatchToProps)(LandingBlockColum));