import React from "react";
import { bindActionCreators } from "redux";
import { connect } from "react-redux";
import {actionCreators} from "../../store/LandingPage";
import { ChromePicker} from 'react-color';


import Slider from '@material-ui/core/Slider';
import ExpansionPanel from '@material-ui/core/ExpansionPanel';
import ExpansionPanelSummary from '@material-ui/core/ExpansionPanelSummary';
import ExpansionPanelDetails from '@material-ui/core/ExpansionPanelDetails';
import Typography from '@material-ui/core/Typography';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';

import TextField from "@material-ui/core/TextField";
import Grid from "@material-ui/core/Grid";

import Radio from "@material-ui/core/Radio";
import RadioGroup from "@material-ui/core/RadioGroup";
import FormControlLabel from "@material-ui/core/FormControlLabel";
import FormControl from "@material-ui/core/FormControl";
import FormLabel from "@material-ui/core/FormLabel";

class BlockTextProp extends React.Component {

  
  handleEditBlockProp = ( event, value, nameBlockProp, style ,styleName) => { 
    this.props.changeBlockValueF(nameBlockProp, style, value, styleName) 
  }

    render(){  
        return(   
          <Grid container>        
            <Grid item xs={12}>
            <ExpansionPanel>
              <ExpansionPanelSummary
                expandIcon={<ExpandMoreIcon />}
                aria-controls="panel1a-content"
                id="panel1a-header"
              >
                <Typography component={'span'}>Текст блока</Typography>
              </ExpansionPanelSummary>
              <ExpansionPanelDetails>
                <Grid container>
                  <Grid item xs={12}>
                      <TextField
                        label="текст заголовка"
                        name="mainText"
                        value={this.props.landingPage.selectBlock.params.mainText.text}
                        variant="outlined"                              
                        onChange={e => this.props.changeBlockValueF(e.target.name, "text", e.target.value)}
                        fullWidth={true}
                        margin="normal"
                        multiline
                        rows="10"
                      />                      
                  </Grid>  
                  <Grid item xs={12}>
                  <FormControl >
                    <FormLabel component="legend">Шрифт</FormLabel>
                    <RadioGroup
                      aria-label="gender"
                      name="fontFamily"
                      value={this.props.landingPage.selectBlock.params.mainText.style.fontFamily}
                      onChange={(nullE, value) => this.handleEditBlockProp('null', value, "mainText", "style","fontFamily")}
                    >
                      <FormControlLabel value="serif" control={<Radio />} label="serif" />
                      <FormControlLabel value="sans-serif" control={<Radio />} label="sans-serif" />
                      <FormControlLabel value="monospace" control={<Radio />} label="monospace" />
                      <FormControlLabel value="cursive" control={<Radio />} label="cursive" />
                      <FormControlLabel value="fantasy" control={<Radio />} label="fantasy" />
                      <FormControlLabel value="system-ui" control={<Radio />} label="system-ui" />                      
                      <FormControlLabel value="inherit" control={<Radio />} label="inherit" />
                    </RadioGroup>
                  </FormControl>
                </Grid>  
                  <Grid item xs={12}>         
                    <Typography component={'span'} id="discrete-slider" gutterBottom>
                        Цвет текста
                        <ChromePicker  color={ this.props.landingPage.selectBlock.params.mainText.style.color }
                        onChangeComplete={(color) => this.handleEditBlockProp('null', color.hex, "mainText", "style", "color")} />
                    </Typography>
                    </Grid>  
                    <Grid item xs={12}>
                      <Typography component={'span'} id="discrete-slider" gutterBottom>
                        Размер текста: {this.props.landingPage.selectBlock.params.mainText.style.fontSize}
                        
                      <Slider
                        defaultValue={this.props.landingPage.selectBlock.params.mainText.style.fontSize}
                        aria-labelledby="discrete-slider"
                        valueLabelDisplay="auto"
                        onChange={(event, fontSize) => this.handleEditBlockProp(event, fontSize, "mainText", "style", "fontSize")}
                        step={2}
                        marks
                        min={12}
                        max={64}
                      />
                      </Typography>
                    </Grid>  
                    <Grid item xs={12}>
                      <Typography component={'span'} id="discrete-slider" gutterBottom>
                        Ширина блока с текстом: {this.props.landingPage.selectBlock.params.mainText.textWidht.number}
                        
                      <Slider
                        defaultValue={this.props.landingPage.selectBlock.params.mainText.textWidht.number}
                        aria-labelledby="discrete-slider"
                        valueLabelDisplay="auto"
                        onChange={(event, number) => this.handleEditBlockProp(event, number, "mainText", "textWidht", "number")}
                        step={2}
                        marks
                        min={2}
                        max={12}
                      />
                      </Typography>
                    </Grid>
                    <Grid item xs={12}>
                    <FormControl >
                      <FormLabel component="legend">Выравнивание текста</FormLabel>
                      <RadioGroup
                        aria-label="gender"
                        name="textAlign"
                        value={this.props.landingPage.selectBlock.params.mainText.style.textAlign}
                        onChange={(nullE, value) => this.handleEditBlockProp(nullE, value, "mainText", "style", "textAlign")}
                      >
                        <FormControlLabel value="left" control={<Radio />} label="Слева" />
                        <FormControlLabel value="center" control={<Radio />} label="Центр" />
                        <FormControlLabel value="right" control={<Radio />} label="Справа" />          
                        <FormControlLabel value="justify" control={<Radio />} label="Поширине" />
                      </RadioGroup>
                    </FormControl>
                    </Grid>
                  </Grid>
                </ExpansionPanelDetails>
              </ExpansionPanel>  
            </Grid> 

            <Grid item xs={12}>
            <ExpansionPanel>
              <ExpansionPanelSummary
                expandIcon={<ExpandMoreIcon />}
                aria-controls="panel1a-content"
                id="panel1a-header"
              >
                <Typography component={'span'}>Цвет фона</Typography>
              </ExpansionPanelSummary>
              <ExpansionPanelDetails>
                <Grid container>              
                    <Typography component={'span'} id="discrete-slider" gutterBottom>
                        Выберите цвет фона для блока
                        <ChromePicker  color={ this.props.landingPage.selectBlock.params.mainText.background.backgroundColor }
                        onChangeComplete={(color) => this.handleEditBlockProp('null', color.hex, "mainText", "background", "backgroundColor")} />
                    </Typography>                   
                  </Grid>
                </ExpansionPanelDetails>
              </ExpansionPanel>  
            </Grid>                 
          </Grid>
        );
    }
}

function mapStateToProps(state) {
    return {
      landingPage: state.landingPage,
  
    }    
}

function matchDispatchToProps(dispatch){
    return bindActionCreators(actionCreators,dispatch)    
}



export default connect(mapStateToProps, matchDispatchToProps)(BlockTextProp);