import React from "react";
import { bindActionCreators } from "redux";
import { connect } from "react-redux";
import { actionCreators } from "../../store/LandingPage";
import TextField from "@material-ui/core/TextField";
import Grid from "@material-ui/core/Grid";

import { ChromePicker } from 'react-color';

import FormControlLabel from "@material-ui/core/FormControlLabel";
import FormControl from "@material-ui/core/FormControl";
import FormLabel from "@material-ui/core/FormLabel";
import Typography from '@material-ui/core/Typography';
import Slider from '@material-ui/core/Slider';
import Radio from "@material-ui/core/Radio";
import RadioGroup from "@material-ui/core/RadioGroup";
import ExpansionPanel from '@material-ui/core/ExpansionPanel';
import ExpansionPanelSummary from '@material-ui/core/ExpansionPanelSummary';
import ExpansionPanelDetails from '@material-ui/core/ExpansionPanelDetails';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
class BlockTextProp extends React.Component {

  parseUrl(name, url, value) {

    var parseUrl = value.split('').reverse();
    let i = 0;
    let newArr = [];

    while (i < 11) {
      newArr[i] = parseUrl[i];
      i++;
    }

    var reversed = 'https://www.youtube.com/embed/' + newArr.reverse().join('');

    this.props.changeBlockValueF(name, url, reversed)
  }

  handleEditBlockProp = (event, value, nameBlockProp, style, styleName) => {
    this.props.changeBlockValueF(nameBlockProp, style, value, styleName)
  }


  render() {
    return (
      <Grid container>
        <Grid item xs={12}>
          <ExpansionPanel>
            <ExpansionPanelSummary
              expandIcon={<ExpandMoreIcon />}
              aria-controls="rightColHeader-content"
              id="rightColHeader"
            >
              <Typography component={'span'}>Заголовок</Typography>
            </ExpansionPanelSummary>
            <ExpansionPanelDetails>
              <Grid container>
                <Grid item xs={12}>
                  <TextField
                    label="Название видео"
                    name="video"
                    value={this.props.landingPage.selectBlock.params.video.text}
                    variant="outlined"
                    onChange={e => this.props.changeBlockValueF(e.target.name, "text", e.target.value)}
                    fullWidth={true}
                    margin="normal"
                  />
                </Grid>
                <Grid item xs={12}>
                  <Typography component={'span'} id="discrete-slider" gutterBottom>
                    Цвет текста
                  <ChromePicker color={this.props.landingPage.selectBlock.params.video.style.color}
                      onChangeComplete={(color) => this.handleEditBlockProp('null', color.hex, "video", "style", "color")} />
                  </Typography>
                </Grid>
                <Grid item xs={12}>
                  <Typography component={'span'} id="discrete-slider" gutterBottom>
                    Размер текста: {this.props.landingPage.selectBlock.params.video.style.fontSize}

                    <Slider
                      defaultValue={this.props.landingPage.selectBlock.params.video.style.fontSize}
                      aria-labelledby="discrete-slider"
                      valueLabelDisplay="auto"
                      onChange={(event, fontSize) => this.handleEditBlockProp(event, fontSize, "video", "style", "fontSize")}
                      step={2}
                      marks
                      min={12}
                      max={64}
                    />
                  </Typography>
                </Grid>
                <Grid item xs={12}>
                  <FormControl >
                    <FormLabel component="legend">Выравнивание текста</FormLabel>
                    <RadioGroup
                      aria-label="gender"
                      name="style"
                      value={this.props.landingPage.selectBlock.params.video.style.textAlign}
                      onChange={(nullE, value) => this.handleEditBlockProp(nullE, value, "video", "style", "textAlign")}
                    >
                      <FormControlLabel value="left" control={<Radio />} label="Слева" />
                      <FormControlLabel value="center" control={<Radio />} label="Центр" />
                      <FormControlLabel value="right" control={<Radio />} label="Справа" />
                    </RadioGroup>
                  </FormControl>
                </Grid>
              </Grid>
            </ExpansionPanelDetails>
          </ExpansionPanel>
        </Grid>

        <Grid item xs={12}>
          <ExpansionPanel>
            <ExpansionPanelSummary
              expandIcon={<ExpandMoreIcon />}
              aria-controls="rightColHeader-content"
              id="rightColHeader"
            >
              <Typography component={'span'}>Видео</Typography>
            </ExpansionPanelSummary>
            <ExpansionPanelDetails>
              <Grid container>
                <Grid item xs={12}>
                  <TextField
                    label="Ссылка на видео"
                    name="video"
                    value={this.props.landingPage.selectBlock.params.video.url}
                    variant="outlined"
                    onChange={e => this.parseUrl(e.target.name, "url", e.target.value)}
                    fullWidth={true}
                    margin="normal"
                  />
                </Grid>
                <Grid item xs={12}>
                  <Typography component={'span'} id="discrete-slider" gutterBottom>
                    Ширина видео: {this.props.landingPage.selectBlock.params.video.width.number}

                    <Slider
                      defaultValue={this.props.landingPage.selectBlock.params.video.width.number}
                      aria-labelledby="discrete-slider"
                      valueLabelDisplay="auto"
                      onChange={(event, number) => this.handleEditBlockProp(event, number, "video", "width", "number")}
                      step={2}
                      marks
                      min={2}
                      max={12}
                    />
                  </Typography>
                </Grid>
              </Grid>
            </ExpansionPanelDetails>
          </ExpansionPanel>
        </Grid>
        <Grid item xs={12}>
          <ExpansionPanel>
            <ExpansionPanelSummary
              expandIcon={<ExpandMoreIcon />}
              aria-controls="rightColHeader-content"
              id="rightColHeader"
            >
              <Typography component={'span'}>Контент</Typography>
            </ExpansionPanelSummary>
            <ExpansionPanelDetails>
              <Grid container>
                <Grid item xs={12}>
                  <FormControl >
                    <FormLabel component="legend">Выравнивание контента</FormLabel>
                    <RadioGroup
                      aria-label="gender"
                      name="textAlign"
                      value={this.props.landingPage.selectBlock.params.video.background.justifyContent}
                      onChange={(nullE, value) => this.handleEditBlockProp(nullE, value, "video", "background", "justifyContent")}
                    >
                      <FormControlLabel value="flex-start" control={<Radio />} label="Слева" />
                      <FormControlLabel value="center" control={<Radio />} label="Центр" />
                      <FormControlLabel value="flex-end" control={<Radio />} label="Справа" />
                    </RadioGroup>
                  </FormControl>
                </Grid>
                <Typography component={'span'} id="discrete-slider" gutterBottom>
                  Выберите цвет фона для блока
                                        <ChromePicker color={this.props.landingPage.selectBlock.params.video.background.backgroundColor}
                    onChangeComplete={(color) => this.handleEditBlockProp('null', color.hex, "video", "background", "backgroundColor")} />
                </Typography>
              </Grid>
            </ExpansionPanelDetails>
          </ExpansionPanel>
        </Grid>
      </Grid>
    );
  }
}

function mapStateToProps(state) {
  return {
    landingPage: state.landingPage,

  }
}

function matchDispatchToProps(dispatch) {
  return bindActionCreators(actionCreators, dispatch)
}



export default connect(mapStateToProps, matchDispatchToProps)(BlockTextProp);