import React from "react";
import { bindActionCreators } from "redux";
import { connect } from "react-redux";
import { actionCreators } from "../../store/LandingPage";
import { ChromePicker } from 'react-color';

import TextField from "@material-ui/core/TextField";
import Grid from "@material-ui/core/Grid";

import Slider from '@material-ui/core/Slider';
import ExpansionPanel from '@material-ui/core/ExpansionPanel';
import ExpansionPanelSummary from '@material-ui/core/ExpansionPanelSummary';
import ExpansionPanelDetails from '@material-ui/core/ExpansionPanelDetails';
import Typography from '@material-ui/core/Typography';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import Radio from "@material-ui/core/Radio";
import RadioGroup from "@material-ui/core/RadioGroup";
import FormControlLabel from "@material-ui/core/FormControlLabel";
import FormControl from "@material-ui/core/FormControl";
import FormLabel from "@material-ui/core/FormLabel";

class BlockImgTextProp extends React.Component {

    handleEditBlockProp = (event, value, nameBlockProp, style, styleName) => {
        this.props.changeBlockValueF(nameBlockProp, style, value, styleName)
    }

    render() {
        return (

            <Grid container>
                <Grid item xs={12}>
                    <ExpansionPanel>
                        <ExpansionPanelSummary
                            expandIcon={<ExpandMoreIcon />}
                            aria-controls="rightColHeader-content"
                            id="rightColHeader"
                        >
                            <Typography component={'span'}>Заголовок</Typography>
                        </ExpansionPanelSummary>
                        <ExpansionPanelDetails>
                            <Grid container>
                                <Grid item xs={12}>
                                    <TextField
                                        label="Заголовок"
                                        name="rightColHeader"
                                        value={this.props.landingPage.selectBlock.params.rightColHeader.text}
                                        variant="outlined"
                                        onChange={e => this.props.changeBlockValueF(e.target.name, "text", e.target.value)}
                                        fullWidth={true}
                                        margin="normal"
                                        multiline
                                        rows="4"
                                    />
                                </Grid>
                                <Grid item xs={12}>
                                    <Typography component={'span'} id="discrete-slider" gutterBottom>
                                        Цвет текста
                                <ChromePicker color={this.props.landingPage.selectBlock.params.rightColHeader.style.color}
                                            onChangeComplete={(color) => this.handleEditBlockProp('null', color.hex, "rightColHeader", "style", "color")} />
                                    </Typography>
                                </Grid>
                                <Grid item xs={12}>
                                    <Typography component={'span'} id="discrete-slider" gutterBottom>
                                        Размер текста: {this.props.landingPage.selectBlock.params.rightColHeader.style.fontSize}

                                        <Slider
                                            defaultValue={this.props.landingPage.selectBlock.params.rightColHeader.style.fontSize}
                                            aria-labelledby="discrete-slider"
                                            valueLabelDisplay="auto"
                                            onChange={(event, fontSize) => this.handleEditBlockProp(event, fontSize, "rightColHeader", "style", "fontSize")}
                                            step={2}
                                            marks
                                            min={12}
                                            max={64}
                                        />
                                    </Typography>
                                </Grid>
                            </Grid>
                        </ExpansionPanelDetails>
                    </ExpansionPanel>

                    <ExpansionPanel>
                        <ExpansionPanelSummary
                            expandIcon={<ExpandMoreIcon />}
                            aria-controls="rightColUnderText-content"
                            id="rightColUnderText"
                        >
                            <Typography component={'span'}>Подтекст</Typography>
                        </ExpansionPanelSummary>
                        <ExpansionPanelDetails>
                            <Grid container>
                                <Grid item xs={12}>
                                    <TextField
                                        label="Подтекст"
                                        name="rightColUnderText"
                                        value={this.props.landingPage.selectBlock.params.rightColUnderText.text}
                                        variant="outlined"
                                        onChange={e => this.props.changeBlockValueF(e.target.name, "text", e.target.value)}
                                        fullWidth={true}
                                        margin="normal"
                                        multiline
                                        rows="4"
                                    />
                                </Grid>
                                <Grid item xs={12}>
                                    <Typography component={'span'} id="discrete-slider" gutterBottom>
                                        Цвет текста
                                <ChromePicker color={this.props.landingPage.selectBlock.params.rightColUnderText.style.color}
                                            onChangeComplete={(color) => this.handleEditBlockProp('null', color.hex, "rightColUnderText", "style", "color")} />
                                    </Typography>
                                </Grid>
                                <Grid item xs={12}>
                                    <Typography component={'span'} id="discrete-slider" gutterBottom>
                                        Размер текста: {this.props.landingPage.selectBlock.params.rightColUnderText.style.fontSize}

                                        <Slider
                                            defaultValue={this.props.landingPage.selectBlock.params.rightColUnderText.style.fontSize}
                                            aria-labelledby="discrete-slider"
                                            valueLabelDisplay="auto"
                                            onChange={(event, fontSize) => this.handleEditBlockProp(event, fontSize, "rightColUnderText", "style", "fontSize")}
                                            step={2}
                                            marks
                                            min={12}
                                            max={64}
                                        />
                                    </Typography>
                                </Grid>
                            </Grid>
                        </ExpansionPanelDetails>
                    </ExpansionPanel>

                    <ExpansionPanel>
                        <ExpansionPanelSummary
                            expandIcon={<ExpandMoreIcon />}
                            aria-controls="rightColMainText-content"
                            id="rightColMainText"
                        >
                            <Typography component={'span'}>Основной текст</Typography>
                        </ExpansionPanelSummary>
                        <ExpansionPanelDetails>
                            <Grid container>
                                <Grid item xs={12}>
                                    <TextField
                                        label="Основной текст"
                                        name="rightColMainText"
                                        value={this.props.landingPage.selectBlock.params.rightColMainText.text}
                                        variant="outlined"
                                        onChange={e => this.props.changeBlockValueF(e.target.name, "text", e.target.value)}
                                        fullWidth={true}
                                        margin="normal"
                                        multiline
                                        rows="4"
                                    />
                                </Grid>
                                <Grid item xs={12}>
                                    <Typography component={'span'} id="discrete-slider" gutterBottom>
                                        Цвет текста
                                <ChromePicker color={this.props.landingPage.selectBlock.params.rightColMainText.style.color}
                                            onChangeComplete={(color) => this.handleEditBlockProp('null', color.hex, "rightColMainText", "style", "color")} />
                                    </Typography>
                                </Grid>
                                <Grid item xs={12}>
                                    <Typography component={'span'} id="discrete-slider" gutterBottom>
                                        Размер текста: {this.props.landingPage.selectBlock.params.rightColMainText.style.fontSize}

                                        <Slider
                                            defaultValue={this.props.landingPage.selectBlock.params.rightColMainText.style.fontSize}
                                            aria-labelledby="discrete-slider"
                                            valueLabelDisplay="auto"
                                            onChange={(event, fontSize) => this.handleEditBlockProp(event, fontSize, "rightColMainText", "style", "fontSize")}
                                            step={2}
                                            marks
                                            min={12}
                                            max={64}
                                        />
                                    </Typography>
                                </Grid>
                            </Grid>
                        </ExpansionPanelDetails>
                    </ExpansionPanel>

                    <ExpansionPanel>
                        <ExpansionPanelSummary
                            expandIcon={<ExpandMoreIcon />}
                            aria-controls="rightColMainText-content"
                            id="rightColMainText"
                        >
                            <Typography component={'span'}>Форматирование текста</Typography>
                        </ExpansionPanelSummary>
                        <ExpansionPanelDetails>
                            <Grid container>
                                <Grid item xs={12}>
                                    <Typography component={'span'} id="discrete-slider" gutterBottom>
                                        Ширина блока с текстом: {this.props.landingPage.selectBlock.params.rightCol.textWidht.number}

                                        <Slider
                                            defaultValue={this.props.landingPage.selectBlock.params.rightCol.textWidht.number}
                                            aria-labelledby="discrete-slider"
                                            valueLabelDisplay="auto"
                                            onChange={(event, number) => this.handleEditBlockProp(event, number, "rightCol", "textWidht", "number")}
                                            step={2}
                                            marks
                                            min={2}
                                            max={12}
                                        />
                                    </Typography>
                                </Grid>
                                <Grid item xs={12}>
                                    <FormControl >
                                        <FormLabel component="legend">Выравнивание текста</FormLabel>
                                        <RadioGroup
                                            aria-label="gender"
                                            name="textAlign"
                                            value={this.props.landingPage.selectBlock.params.rightCol.background.textAlign}
                                            onChange={(nullE, value) => this.handleEditBlockProp(nullE, value, "rightCol", "background", "textAlign")}
                                        >
                                            <FormControlLabel value="left" control={<Radio />} label="Слева" />
                                            <FormControlLabel value="center" control={<Radio />} label="Центр" />
                                            <FormControlLabel value="right" control={<Radio />} label="Справа" />
                                        </RadioGroup>
                                    </FormControl>
                                </Grid>
                                <Grid container>
                                    <Typography component={'span'} id="discrete-slider" gutterBottom>
                                        Выберите цвет фона для блока
                                        <ChromePicker color={this.props.landingPage.selectBlock.params.rightCol.background.backgroundColor}
                                            onChangeComplete={(color) => this.handleEditBlockProp('null', color.hex, "rightCol", "background", "backgroundColor")} />
                                    </Typography>
                                </Grid>
                            </Grid>
                        </ExpansionPanelDetails>
                    </ExpansionPanel>
                </Grid>

                <Grid item xs={12}>
                    <ExpansionPanel>
                        <ExpansionPanelSummary
                            expandIcon={<ExpandMoreIcon />}
                            aria-controls="leftCol-content"
                            id="leftCol"
                        >
                            <Typography component={'span'}>Изображение</Typography>
                        </ExpansionPanelSummary>
                        <ExpansionPanelDetails>
                            <Grid container>
                                <TextField
                                    label="Ссылка на изображение"
                                    name="leftCol"
                                    value={this.props.landingPage.selectBlock.params.leftCol.imgUrl}
                                    variant="outlined"
                                    onChange={e => this.props.changeBlockValueF(e.target.name, "imgUrl", e.target.value)}
                                    fullWidth={true}
                                    multiline
                                    rows="3"
                                    margin="normal"
                                />
                            </Grid>
                        </ExpansionPanelDetails>
                    </ExpansionPanel>
                </Grid>
            </Grid>
        );
    }
}

function mapStateToProps(state) {
    return {
        landingPage: state.landingPage,

    }
}

function matchDispatchToProps(dispatch) {
    return bindActionCreators(actionCreators, dispatch)
}



export default connect(mapStateToProps, matchDispatchToProps)(BlockImgTextProp);