import React from "react";
import { withStyles } from "@material-ui/core/styles";
import PropTypes from "prop-types";
import { bindActionCreators } from "redux";
import { connect } from "react-redux";
import {actionCreators} from "../../store/LandingPage";

import Grid from "@material-ui/core/Grid";

const styles = theme => ({  
    textCenter: {
        textAlign: "center",
        fontSize: "24px"
    },
    videoConteiner: {
        position:'relative',
        paddingBottom:'56.25%',
        paddingTop:'30px',
        height:'0',
        overflow:'hidden',
    },
    iframeYoutube: {
        position:'absolute',
        top:'0',
        left:'0',
        width:'100%',
        height:'100%',
    }
});

class LandingBlockVideoText extends React.Component {
    
    render() { 
        const {classes} = this.props; 
        return (         
            <Grid item xs={12} sm={12} container>
                <Grid item xs={12} sm={this.props.landingPage.selectLanding.blocks[this.props.itemNum].params.video.width.number}>  
                    <div style={this.props.landingPage.selectLanding.blocks[this.props.itemNum].params.video.style}>{this.props.landingPage.selectLanding.blocks[this.props.itemNum].params.video.text}</div>                
                    <div className={classes.videoConteiner}>
                        <iframe className={classes.iframeYoutube} width="560" height="315" src={this.props.landingPage.selectLanding.blocks[this.props.itemNum].params.video.url} frameborder="0" allowfullscreen title={this.props.landingPage.selectLanding.blocks[this.props.itemNum].params.video.text}></iframe>
                    </div>
                </Grid>
               <Grid style={this.props.landingPage.selectLanding.blocks[this.props.itemNum].params.leftCol.background} item xs={this.props.landingPage.selectLanding.blocks[this.props.itemNum].params.leftCol.textWidht.number}>
                    <div item style={this.props.landingPage.selectLanding.blocks[this.props.itemNum].params.leftColHeader.style} container >
                        {this.props.landingPage.selectLanding.blocks[this.props.itemNum].params.leftColHeader.text}
                    </div>
                    <div item style={this.props.landingPage.selectLanding.blocks[this.props.itemNum].params.leftColUnderText.style} container >
                        {this.props.landingPage.selectLanding.blocks[this.props.itemNum].params.leftColUnderText.text}
                    </div>
                    <div item style={this.props.landingPage.selectLanding.blocks[this.props.itemNum].params.leftColMainText.style} container >
                        {this.props.landingPage.selectLanding.blocks[this.props.itemNum].params.leftColMainText.text}
                    </div>
                </Grid>
                
            </Grid>     
         )
    }
}

LandingBlockVideoText.propTypes = {
    classes: PropTypes.object.isRequired,
    
};

function mapStateToProps(state) {
    return { 
        landingPage: state.landingPage,
    }
}

function matchDispatchToProps(dispatch){
    return bindActionCreators(actionCreators,dispatch)    
}


export default withStyles(styles)(connect(mapStateToProps, matchDispatchToProps)(LandingBlockVideoText));